/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react';
import { SafeAreaView, Text, View } from 'react-native';
import { useSelector } from 'react-redux';
import { useDeviceSize } from 'rn-responsive-styles';
import { AccountScreenStyle } from './AccountScreen.style';
import Separator from '../common/ui/Separator';
import RoundButton from '../common/ui/RoundButton';
import translations from '../../resources/locales/translations';

export const AccountScreen = () => {
  // Start manage localization
  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  //To get user details data
  const userDetails = useSelector(
    (state: any) => state?.Appreducer?.userDetails
  );

  //To get the Device Size
  const deviceSize = useDeviceSize();
  const accountdStyle = AccountScreenStyle(deviceSize);

  return (
    <SafeAreaView style={accountdStyle.main}>
      <View style={accountdStyle.mainView}>
        <Text style={accountdStyle.titleTextStyle}>{str('aboutAccount')}</Text>
        <RoundButton
          title={str('manage')}
          size="small"
        />
      </View>

      <View style={accountdStyle.containerView}>
        <Text style={accountdStyle.subTitleTextStyle}>{str('demoAccount')}</Text>
        <Text style={accountdStyle.descriptionTextStyle}>
          {userDetails?.demoAccountCreated}/{userDetails?.demoLimit}
        </Text>
        <Text style={accountdStyle.descriptionTextStyle}>
        {str('temporaryAccounts')}
        </Text>
        {/* To Show the dash line */}
        <Separator />
        <Text style={accountdStyle.subTitleTextStyle}>{str('liveAccount')}</Text>
        <Text style={accountdStyle.descriptionTextStyle}>
          {userDetails?.liveAccountCreated}/{userDetails?.liveLimit}
        </Text>
        <Text style={accountdStyle.descriptionTextStyle}>
        {str('temporaryAccounts')}
        </Text>
        {/* To Show the dash line */}
        <Separator />
        <Text style={accountdStyle.subTitleTextStyle}>{str('alternativeNames')}</Text>
      </View>
    </SafeAreaView>
  );
};
export default AccountScreen;
