/* eslint-disable prettier/prettier */

import React, { useState } from 'react';
import { TouchableOpacity, Text, View, StyleSheet, Platform } from 'react-native';
import { gray,  grayBG,  paginationBG, white } from '../../../resources/values/color';
import { Responsive } from '../../../utils/Responsive';
import Fonts from '../../../utils/Fonts';


const CustomPagination = ({ currentPage, totalPages, onNext, onPagePress } : { currentPage :  any, totalPages :  any, onNext  :  any, onPagePress :  any }) => {
    const pageNumbers = Array.from({ length: totalPages }, (_, index) => index + 1);

    return (
        <View style={styles.paginationContainer}>
            <View
                style={styles.paginationButton}
                // onPress={onPrev}
                // disabled={currentPage === 1}
            >
                <Text>Page 1 of 766</Text>
            </View>
            <View style={styles.pageNumbersContainer}>
                {pageNumbers.map((page) => (
                    <TouchableOpacity
                        key={page}
                        style={[
                            styles.pageNumberButton,
                            currentPage === page ? styles.selectedPageNumberButton : styles.unSelectedPageNumberButton,
                        ]}
                        onPress={() => onPagePress(page)}
                    >
                        <Text style={currentPage === page ? styles.selectPageNumberText : styles.unSelectPageNumberText}>{page}</Text>
                    </TouchableOpacity>
                ))}
            </View>
            <TouchableOpacity
                style={styles.paginationButton}
                onPress={onNext}
                disabled={currentPage === totalPages}
            >
                <Text>Next</Text>
            </TouchableOpacity>
        </View>
    );
};

const styles = StyleSheet.create({
    paginationContainer: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: 10,
        marginTop: 10,
    },
    paginationButton: {
        padding: 10,
    },
    pageNumbersContainer: {
        flexDirection: 'row',
    },
    pageNumberButton: {
        borderRadius: Responsive.size20 / 2,
        paddingVertical: Responsive.size5 / 2,
        paddingHorizontal: Responsive.size10 / 2,
        marginHorizontal: Responsive.size5 / 2,
        backgroundColor: '#f0f0f0',
        justifyContent : 'center',
        alignSelf: 'center',
    },
    selectedPageNumberButton: {
        backgroundColor: paginationBG,
        borderRadius: Responsive.size20 / 2,
    },
    unSelectedPageNumberButton: {
        backgroundColor: grayBG,
        borderRadius: Responsive.size20 / 2,
        paddingVertical: Responsive.size5 / 2,
        paddingHorizontal: Responsive.size10 / 2,
        marginHorizontal: Responsive.size5 / 2,
    },
    selectPageNumberText: {
        color: white,
        letterSpacing : 1.8,
        fontSize : Responsive.size18 / 2,
        lineHeight : Responsive.size23 / 2,
        fontFamily: Platform.OS === 'web' ? 'medium' : Fonts.medium,
    },
    unSelectPageNumberText: {
        color: gray,
        letterSpacing: 1.8,
        fontSize: Responsive.size18 / 2,
        lineHeight: Responsive.size23 / 2,
        fontFamily: Platform.OS === 'web' ? 'medium' : Fonts.medium,
    },
});


const Vikas = () => {
    const [currentPage, setCurrentPage] = useState(1);
    const totalPages = 10;

    const handleNext = () => {
        setCurrentPage((prevPage) => Math.min(prevPage + 1, totalPages));
    };

    // const handlePrev = () => {
    //     setCurrentPage((prevPage) => Math.max(prevPage - 1, 1));
    // };

    const handlePagePress = (page: React.SetStateAction<number>) => {
        setCurrentPage(page);
    };

    return (
        <View>
            {/* <Text>{`Current Page: ${currentPage}`}</Text> */}

            <CustomPagination
                currentPage={currentPage}
                totalPages={totalPages}
                onNext={handleNext}
                // onPrev={handlePrev}
                onPagePress={handlePagePress}
            />
        </View>
    );
};

export default Vikas;

