/* eslint-disable prettier/prettier */
/* eslint-disable react/react-in-jsx-scope */
import { Pressable, Text } from 'react-native';
import { RoundButtonStyle } from '../RoundButton.style';
import { useDeviceSize } from 'rn-responsive-styles';

const RoundButton = (props: any) => {
    const deviceSize = useDeviceSize();
    const roundButtonStyle = RoundButtonStyle(deviceSize);

    const {
        title,
        size,
    } = props;
    return (
        <Pressable>
            <Text style={size === 'big' ? roundButtonStyle.bigBtnStyle : roundButtonStyle.smallBtnStyle}>{title}</Text>
        </Pressable>
    );
};
export default RoundButton;
