// CustomBottomSheet.js
import React from 'react';
import { View, Text, Modal } from 'react-native';
import { CustomBottomSheetStyle } from '../CustomBottomSheet.style';
import RegularButton from './RegularButton';

const CustomBottomSheet = ({ isVisible, onPressBtn, title, btnText, children }: any) => {
    return (
        <Modal
            transparent
            visible={isVisible}
            animationType="slide"
            onRequestClose={onPressBtn}>
            <View style={CustomBottomSheetStyle.overlay}>
                <View style={CustomBottomSheetStyle.content}>
                    <Text style={CustomBottomSheetStyle.textTitle}>{title}</Text>
                    {children}
                    <RegularButton
                        title={btnText}
                        border={false}
                        onPress={() => onPressBtn()}
                    />
                </View>
            </View>
        </Modal>
    );
};

export default CustomBottomSheet;
