/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */

import React, { useEffect, useState } from 'react';
import {
  Animated,
  Image,
  Platform,
  Pressable,
  SafeAreaView,
  ScrollView,
  StatusBar,
  Text,
  View,
} from 'react-native';
import { localAssets } from '../../../resources/assets/assets';
import {
  primaryTop,
} from '../../../resources/values/color';
import { StatusbarStyle } from '../../../utils/statusbarStyle';
import GradientComponent from '../../common/ui/Gradient';
import { reset } from '../../../routes/Navigator';
import { isEmpty, isValidEmail, isValidPassword } from '../../../utils/utils';
import { useDispatch, useSelector } from 'react-redux';
import { API_LOGIN } from '../../../services/API/ApiConstants';
import {
  loggedIn,
  setRefreshToken,
  setToken,
} from '../../../services/app/Appreducer';
import { postApi } from '../../../services/API/Api-Helper';
import {
  addEmail,
  addErrorEmail,
  addErrorPassword,
  addPassword,
  addRememberMe,
  clearState,
} from './signin.reducer';
import CustomToast from '../../common/ui/CustomToast';
import CustomLoader from '../../common/ui/CustomLoader';
import { useDeviceSize } from 'rn-responsive-styles';
import { SignInScreenStyle } from './SignInScreen.style';
import InputRegularText from '../../common/ui/InputRegularText';
import RegularButton from '../../common/ui/RegularButton';
import translations from '../../../resources/locales/translations';
import { ROUTE_LANDING } from '../../../routes/RouteConst';

const SignInScreen = () => {

  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  //To get the current size of screen
  const deviceSize = useDeviceSize();
  const signInStyle = SignInScreenStyle(deviceSize);
  const [animation] = useState(new Animated.Value(0));
  const [toastVisible, setToastVisible] = useState(false);
  const [toastMessage, setToastMessage] = useState('');
  const [loading, setLoading] = useState(false);

  //To show the toast message
  const showToast = (message: any) => {
    setToastMessage(message);
    setToastVisible(true);
    setTimeout(() => {
      setToastVisible(false);
    }, 3000);
  };

  const ipEmail: any = React.useRef(null);
  const ipPassword: any = React.useRef(null);

  const { email, password, rememberMe } = useSelector(
    (state: {
      signInReducer: {
        email: string;
        errorEmail: string;
        password: string;
        errorPassword: string;
        rememberMe: boolean;
        loading: boolean;
      };
    }) => state.signInReducer
  );

  const dispatch = useDispatch();

  useEffect(() => {
    if (!rememberMe) {
      dispatch(clearState());
    }
  }, []);

  useEffect(() => {
    handleAnimation();
  }, []);

  // Function for validating login information
  const validateSignInInfo = () => {
    if (isEmpty(email)) {
      showToast(str('enterEmailAddress'));
      ipEmail?.current?.focus();
    } else if (!isValidEmail(email)) {
      showToast(str('enterValidEmailAddress'));
      ipEmail.current.focus();
    } else if (isEmpty(password)) {
      showToast(str('enterPassword'));
      ipPassword.current.focus();
    } else if (!isValidPassword(password)) {
      showToast(str('passwordMinimumCharacterMessgae'));
      ipPassword.current.focus();
    } else {
      setLoading(true);
      login();
    }
  };

  // Function for call Login API
  const login = () => {
    let params = {
      email,
      password,
      type: 1,
    };
    postApi(API_LOGIN, params, callBackSuccess, callBackError);
  };
  // Handle API success callback
  const callBackSuccess = (response: any) => {
    setLoading(false);
    dispatch(loggedIn());
    dispatch(addEmail(email));
    dispatch(addPassword(password));
    dispatch(setToken(response?.data?.access_token));
    dispatch(setRefreshToken(response?.data?.refresh_token));

    reset(ROUTE_LANDING);
  };
  // Handle API failure callback
  const callBackError = (error: string) => {
    showToast(error);
    setLoading(false);
  };

  const animatedStyle = {
    transform: [
      {
        translateY: animation.interpolate({
          inputRange: [0, 1],
          outputRange: [100, 0], // Transition from 100% (bottom) to 0% (top) of the container's height
        }),
      },
    ],
    opacity: animation, // Opacity value
  };

  const handleAnimation = () => {
    // Define the animation configuration
    const animationConfig = {
      toValue: 1, // Final value for both translateY and opacity
      duration: 1000, // Duration of the animation in milliseconds
    };
    // Create parallel animations for translateY and opacity
    const translateYAnimation = Animated.timing(animation, {
      ...animationConfig,
      useNativeDriver: false, // Required for opacity animation
    });
    const opacityAnimation = Animated.timing(animation, {
      ...animationConfig,
      useNativeDriver: false,
    });
    // Start both animations in parallel
    Animated.parallel([translateYAnimation, opacityAnimation]).start();
  };

  return (
    <ScrollView style={signInStyle.mainScroll}>
      <GradientComponent />
      <StatusBar
        backgroundColor={primaryTop}
        barStyle={StatusbarStyle.LIGHT}
        hidden={false}
      />
      <ScrollView
        keyboardShouldPersistTaps="handled"
        scrollEnabled={Platform.OS === 'web' ? true : false}
        style={signInStyle.scrollStyle}
      >
        <SafeAreaView style={signInStyle.mainView}>
          <Image
            resizeMode="contain"
            source={localAssets.logo}
            style={signInStyle.logoView}
          />
          <Text style={signInStyle.titleText}>{str('signIN')}</Text>
          <Text style={signInStyle.descriptionText}>
            {str('signInTitle')}
          </Text>
          {/* For the Animated View */}
          <Animated.View style={[animatedStyle]}>
            <InputRegularText
              title={str('emailAddress')}
              reference={ipEmail}
              returnKeyType={'next'}
              placeholder={str('emailAddress')}
              value={email}
              isPasswordShow={false}
              keyboardType="email-address"
              onChangeText={(text: string) => {
                dispatch(addEmail(text));
                dispatch(addErrorEmail(''));
              }}
              onSubmitEditing={() => {
                ipPassword.current.focus();
              }}
            />
            <InputRegularText
              title={str('password')}
              reference={ipPassword}
              placeholder={str('password')}
              value={password}
              isPasswordShow={true}
              returnKeyType={'done'}
              onChangeText={(text: string) => {
                dispatch(addPassword(text));
                dispatch(addErrorPassword(''));
              }}
              onSubmitEditing={() => validateSignInInfo()}
            />

            <View style={signInStyle.checkBox}>
              <Pressable onPress={() => dispatch(addRememberMe(!rememberMe))}>
                <View style={signInStyle.rowView}>
                  <Image
                    source={
                      rememberMe
                        ? localAssets.checkBoxSelected
                        : localAssets.checkbox
                    }
                    style={signInStyle.checkBoxContainer}
                    resizeMode={'contain'}
                  />
                  <Text style={signInStyle.checkBoxView}>{str('rememberMe')}</Text>
                </View>
              </Pressable>
              {deviceSize === 'extralarge' || deviceSize === 'large' ? (
                <Pressable>
                  <Text style={signInStyle.forgotPasswordText}>
                    {str('forgotPassword')}
                  </Text>
                </Pressable>
              ) : null}
            </View>
            <RegularButton
              title={str('login')}
              border={false}
              onPress={() => validateSignInInfo()}
            />
            {deviceSize === 'medium' ||
              deviceSize === 'small' ||
              deviceSize === 'extrasmall' ? (
              <Pressable style={signInStyle.forgotPasswordView}>
                <Text style={signInStyle.forgotPasswordText}>
                  {str('forgotPassword')}
                </Text>
              </Pressable>
            ) : null}

            {deviceSize === 'extralarge' || deviceSize === 'large' ? (
              <RegularButton
                title={str('resendConfirmationEmail')}
                border={true}
                onPress={() => { }}
              />
            ) : null}
          </Animated.View>

          {toastVisible && <CustomToast message={toastMessage} />}
          {loading && <CustomLoader />}
        </SafeAreaView>
      </ScrollView>

      {deviceSize === 'medium' ||
        deviceSize === 'small' ||
        deviceSize === 'extrasmall' ? (
        <Pressable style={signInStyle.resendEmailButtonSmall}>
          <Text style={signInStyle.resendEmailTextSmall}>
            {str('resendConfirmationEmail')}
          </Text>
        </Pressable>
      ) : null}
    </ScrollView>
  );
};

export default SignInScreen;
