import { Image, Pressable, Text, TextInput, View } from 'react-native';

import { InputRegularTextStyle } from '../InputRegularText.style';
import React, { useState } from 'react';
import { localAssets } from '../../../resources/assets/assets';
import { lighterGray } from '../../../resources/values/color';

const InputRegularText = (props: any) => {
  const {
    border,
    title,
    style,
    reference,
    placeholder,
    value,
    onChangeText,
    onSubmitEditing,
    isPasswordShow,
    returnKeyType,
    keyboardType,
  } = props;
  const inputRef = React.useRef();
  const [hidePass, setHidePass] = useState(isPasswordShow);
  return (
    <>
      <View style={InputRegularTextStyle.mainContainer}>
        {!border &&
          <Text style={InputRegularTextStyle.textStyle}>{title}</Text>
        }
        <View style={border ? InputRegularTextStyle.placeholderTextBorder : InputRegularTextStyle.placeholderText}>
          <TextInput
            style={style || InputRegularTextStyle.textInput}
            ref={reference || inputRef}
            returnKeyType={returnKeyType}
            placeholder={placeholder || ''}
            placeholderTextColor={lighterGray}
            value={value}
            keyboardType={keyboardType}
            secureTextEntry={hidePass ? true : false}
            onChangeText={(text) => {
              onChangeText(text);
            }}
            onSubmitEditing={(text) => {
              onSubmitEditing(text);
            }}
          />
          {isPasswordShow === false ? null : (
            <Pressable
              style={InputRegularTextStyle.passwordShowHide}
              onPress={() => {
                setHidePass(!hidePass);
              }}
            >
              <Image
                source={hidePass ? localAssets.eye : localAssets.eyeClosed}
                style={InputRegularTextStyle.password}
                resizeMode={'contain'}
              />
            </Pressable>
          )}
        </View>
      </View>
    </>
  );
};

export default InputRegularText;
