/* eslint-disable prettier/prettier */
import ApiService from './ApiService';
import {METHOD} from './ApiTypes';

// Function for execute post API
const postApi = (
  endPoint: string,
  params: any,
  callBackSuccess?: any,
  callBackError?: any,
) => {
  ApiService(
    endPoint,
    params,
    async (response: any) => {
      callBackSuccess(response);
    },
    (error: any) => {
      callBackError(error);
    },
    METHOD.POST,
  );
};

// Function for execute get API
const getApi = (
  endPoint: string,
  params?: any,
  callBackSuccess?: any,
  callBackError?: any,
  base? : string,
) => {
  ApiService(
    endPoint,
    params,
    async (response: any) => {
      callBackSuccess(response);
    },
    (error: any) => {
      callBackError(error);
    },
    METHOD.GET,
    base
  );
};

