/* eslint-disable prettier/prettier */
import { createSlice } from '@reduxjs/toolkit';

export interface DashboardReducer {
    loadingHome: boolean
    EAUpdatesList: []
    imagesList:[]
}

const initialState: DashboardReducer = {
    loadingHome: false,
    EAUpdatesList: [],
    imagesList:[],
};

export const DashboardReducer = createSlice({
    name: 'DashboardReducer',
    initialState,
    reducers: {
        loadingHome: (state, action) => {
            state.loadingHome = action.payload;
        },
        EAUpdatesList: (state, action) => {
            state.EAUpdatesList = action.payload;
        },
        imagesList: (state, action) => {
            state.imagesList = action.payload;
        },
        clearHomeScreenReducer: () => {
            return initialState;
        },
    },
});

export const { loadingHome, EAUpdatesList,imagesList, clearHomeScreenReducer } = DashboardReducer.actions;

export default DashboardReducer.reducer;
