/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/react-in-jsx-scope */
import { SafeAreaView, View, Text, Image } from 'react-native';
import { useSelector } from 'react-redux';
import { useDeviceSize } from 'rn-responsive-styles';
import { AttendanceScreenStyle } from './AttendanceScreen.style';
import { useEffect, useState } from 'react';
import translations from '../../resources/locales/translations';

export const AttendanceScreen = () => {
  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  //To get user details data
  const userData = useSelector((state: any) => state?.Appreducer?.userDetails);

  // To get current Device Size
  const deviceSize = useDeviceSize();
  const attedanceStyle = AttendanceScreenStyle(deviceSize);

  return (
    <SafeAreaView style={attedanceStyle.main}>
      <View>
        {deviceSize === 'extralarge' || deviceSize === 'large' ? (
          <Text style={attedanceStyle.titleText}>{str('attendance')}</Text>
        ) : null}
        <View style={attedanceStyle.mainContainer}>
          <View style={attedanceStyle.attendanceView}>
            {deviceSize === 'medium' ||
              deviceSize === 'small' ||
              deviceSize === 'extrasmall' ? (
              <Text style={attedanceStyle.attedanceTitle}>{str('attendance')}</Text>
            ) : null}
            {/* To show the QR code */}
            <Image
              source={{
                uri: encodeURI(
                  'https://chart.googleapis.com/chart?chs=200x200&cht=qr&chl=' +
                  JSON.stringify({ QR: userData.QR, id: userData.id })
                ),
              }}
              style={attedanceStyle.qrView}
            />
          </View>

          <View style={attedanceStyle.stackView}>
            <View style={attedanceStyle.secondStackViewGreen}>
              <Text style={attedanceStyle.stackText}>{str('registerFF')}</Text>
            </View>
            <View style={attedanceStyle.secondStackView}>
              <Text style={attedanceStyle.stackViewText}>{str('gemsPoint')}</Text>
            </View>
          </View>
          <View style={attedanceStyle.secondStackViewGreen}>
            <Text style={attedanceStyle.stackText}>{str('gift')}</Text>
          </View>
        </View>
      </View>
    </SafeAreaView>
  );
};

export default AttendanceScreen;
