import { localAssets } from '../../resources/assets/assets';
import { View, Image, FlatList } from 'react-native';
import { useDeviceSize } from 'rn-responsive-styles';
import { VPSListStyle } from './VPSListScreen.style';
import RoundButton from '../common/ui/RoundButton';
import { useEffect, useState } from 'react';
import translations from '../../resources/locales/translations';
import { useSelector } from 'react-redux';

// VPS static list
const VPSList = [
  {
    id: '1',
    title: 'First Item',
  },
  {
    id: '2',
    title: 'Second Item',
  },
  {
    id: '3',
    title: 'Third Item',
  },
  {
    id: '4',
    title: 'Fourth Item',
  },
  {
    id: '5',
    title: 'Fifth Item',
  },
  {
    id: '6',
    title: 'Sixth Item',
  },
  {
    id: '7',
    title: 'Seventh Item',
  },
  {
    id: '8',
    title: 'Eight Item',
  },
];

export const VPSListScreen = () => {

  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  // To get the current device size
  const deviceSize = useDeviceSize();

  const vpsStyle = VPSListStyle(deviceSize);

  const renderVPSItem = () => {
    return (
      <View style={vpsStyle.mainView}>
        <Image source={localAssets.server} style={vpsStyle.listImageView} />
        <View style={vpsStyle.imageView}>
          <Image source={localAssets.aws} style={vpsStyle.imageStyle} />
          <View style={vpsStyle.btnOuter}>
            <RoundButton
              title={str('startCreateVPS')}
              size="big"
            />
          </View>
        </View>
      </View>
    );
  };

  return (
    <View style={vpsStyle.flatListView}>
      {/* To show the list of VPS */}
      <FlatList
        horizontal
        showsHorizontalScrollIndicator={true}
        data={VPSList}
        renderItem={({ }) => renderVPSItem()}
        keyExtractor={(item) => item.id}
        scrollEnabled={true}
      />
    </View>
  );
};

export default VPSListScreen;
