/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/react-in-jsx-scope */
import { Animated, Image, ScrollView, StatusBar, Text, View } from 'react-native';
import { localAssets } from '../../resources/assets/assets';
import { useDeviceSize } from 'rn-responsive-styles';
import { primaryTop } from '../../resources/values/color';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import ComingSoonScreen from '../common/ui/ComingSoonScreen';
import Dashboard from '../Dashboard/DashboardScreen';
import AlertDialog from '../common/ui/AlertDialog';
import { addPassword } from '../Authentication/SignIn/signin.reducer';
import { loggedOut, setAppLanguage } from '../../services/app/Appreducer';
import { navigate, reset } from '../../routes/Navigator';
import { ROUTE_ACCOUNT, ROUTE_ACTIVITIES, ROUTE_ANNOUNCEMENTS, ROUTE_ATTENDANCE, ROUTE_CLASSES, ROUTE_DASHBOARD, ROUTE_EXPERT_ADVISORS, ROUTE_GIFTS, ROUTE_INDICATORS, ROUTE_LIBRARIES, ROUTE_MOBILE_SIDE_MENU, ROUTE_POINTS, ROUTE_SCRIPTS, ROUTE_SIGNALS, ROUTE_SIGNIN_SCREEN, ROUTE_TICKETS, ROUTE_UTILITIES, ROUTE_VPS_MACHINES } from '../../routes/RouteConst';
import { StatusbarStyle } from '../../utils/statusbarStyle';
import { LandingScreenStyle } from './LandingScreen.style';
import Header from '../common/ui/Header';
import SideMenuItem from '../common/ui/SideMenuItem';
import translations from '../../resources/locales/translations';
import GiftsScreen from '../Gifts/Gifts';
import PointsScreen from '../Points/PointsScreen';
import ClassesScreen from '../Classes/ClassesScreen';


const LandingScreen = (props: any) => {
    // Start manage localization

    // Start manage localization
    const { appLanguage } = useSelector((state: any) => state.Appreducer);
    const [language, setLanguage] = useState<'en' | 'zh'>('en');
    useEffect(() => {
        setLanguage(appLanguage);
    }, []);
    const str = (key: keyof typeof translations.en): string => {
        return translations[language][key];
    };
    // End manage localization

    const [animation] = useState(new Animated.Value(0));
    const deviceSize = useDeviceSize();

    const landingScreenStyle = LandingScreenStyle(deviceSize);

    const userDetails = useSelector((state: any) => state?.Appreducer?.userDetails);

    const dispatch = useDispatch();

    const [activeScreen, setActiveScreen] = useState(props.route.params ? props.route.params : ROUTE_DASHBOARD);
    const navigateToScreen = (screen: any) => {
        setActiveScreen(screen);
    };

    useEffect(() => {
        handleAnimation();
    }, []);

    const handleAnimation = () => {
        // Define the animation configuration
        const animationConfig = {
            toValue: 1, // Final value for both translateX and opacity
            duration: 1000, // Duration of the animation in milliseconds
        };
        // Create parallel animations for translateY and opacity
        const translateYAnimation = Animated.timing(animation, {
            ...animationConfig,
            useNativeDriver: false, // Required for opacity animation
        });
        const opacityAnimation = Animated.timing(animation, {
            ...animationConfig,
            useNativeDriver: false,
        });
        // Start both animations in parallel
        Animated.parallel([translateYAnimation, opacityAnimation]).start();
    };

    const animatedStyle = {
        transform: [
            {
                translateX: animation.interpolate({
                    inputRange: [0, 1],
                    outputRange: [-100, 0],
                }),
            },
        ],
        opacity: animation,
    };

    const renderScreen = () => {
        console.log(activeScreen, 'ascreen');
        switch (activeScreen) {
            case ROUTE_DASHBOARD:
                return <Dashboard />;
            case ROUTE_POINTS:
                return <PointsScreen />;
            case ROUTE_GIFTS:
                return <GiftsScreen />;
            case ROUTE_CLASSES:
                return <ClassesScreen />;
            default:
                return <ComingSoonScreen />;
        }
    };

    const onPressAlert = () => {

    };

    async function logout() {
        AlertDialog.show({
            title: str('logout'),
            message: str('confirmLogout'),
            positiveButton: {
                title: str('yes'),
                onPress: async () => {
                    dispatch((addPassword('')));
                    dispatch(loggedOut());
                    reset(ROUTE_SIGNIN_SCREEN);
                    AlertDialog.hide();
                },
            },
            negativeButton: {
                title: str('no'),
                onPress: async () => {
                    AlertDialog.hide();
                },
            },
        });
    }

    const updateLanguage = (lan: string) => {
        dispatch(setAppLanguage(lan));
    };

    return (
        <View style={landingScreenStyle.mainLanding}>
            <StatusBar backgroundColor={primaryTop} barStyle={StatusbarStyle.LIGHT} hidden={false} />
            <View style={deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                landingScreenStyle.mobileOuter
                :
                landingScreenStyle.webOuter
            }>
                <Header
                    styleMain={landingScreenStyle.mainToolBar}
                    sourceRightImage={localAssets.alert}
                    onPressRight={() => onPressAlert()}
                    sourceCenterImage={localAssets.appLogo}
                    styleCenterImage={landingScreenStyle.appLogoStyle}

                    styleLeftPressable={
                        deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                            landingScreenStyle.centerAlign :
                            null
                    }
                    sourceLeftImage={
                        deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                            localAssets.list :
                            localAssets.logo_blue
                    }
                    styleLeftImage={
                        deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                            landingScreenStyle.iconStyle :
                            landingScreenStyle.logoStyle
                    }
                    onPressLeft={() => {
                        deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                            navigate(ROUTE_MOBILE_SIDE_MENU, activeScreen) :
                            null;
                    }}
                />
            </View>

            <View style={landingScreenStyle.webSideMenuMain}>
                {deviceSize === 'extralarge' || deviceSize === 'large' ?
                    <View style={landingScreenStyle.webSideMenuOuter}>
                        <Animated.View style={[animatedStyle]}>
                            <View style={landingScreenStyle.innerAnimate}>
                                <Image
                                    source={localAssets.logo_white}
                                    style={landingScreenStyle.logoWhite}
                                    resizeMode={'contain'}
                                />
                                <View style={landingScreenStyle.profileOuter}>
                                    <Image
                                        source={userDetails?.avatar ?
                                            { uri: userDetails?.avatar } :
                                            localAssets.profile_web}
                                        style={landingScreenStyle.avtarStyle}
                                        resizeMode={'cover'}
                                    />
                                    <Text style={landingScreenStyle.nameStyle}>
                                        {language === 'en' ? userDetails?.englishFirstName : userDetails?.chineseFirstName} {language === 'en' ? userDetails?.englishLastName : userDetails?.chineseLastName}
                                    </Text>
                                </View>

                                <ScrollView showsVerticalScrollIndicator={false}
                                    style={landingScreenStyle.webSideMenuScroll}>
                                    <View style={landingScreenStyle.fullFlex}>
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_DASHBOARD)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_DASHBOARD}
                                            icon={localAssets.home}
                                            title={str('dashboard')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_POINTS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_POINTS}
                                            icon={localAssets.points}
                                            title={str('Points')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_GIFTS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_GIFTS}
                                            icon={localAssets.gift}
                                            title={str('gifts')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_ANNOUNCEMENTS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_ANNOUNCEMENTS}
                                            icon={localAssets.announcement}
                                            title={str('announcements')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_ACCOUNT)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_ACCOUNT}
                                            icon={localAssets.account}
                                            title={str('account')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_TICKETS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_TICKETS}
                                            icon={localAssets.ticket}
                                            title={str('tickets')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_VPS_MACHINES)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_VPS_MACHINES}
                                            icon={localAssets.vps}
                                            title={str('vpsMachines')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_CLASSES)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_CLASSES}
                                            icon={localAssets.class}
                                            title={str('classes')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_ACTIVITIES)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_ACTIVITIES}
                                            icon={localAssets.activity}
                                            title={str('activities')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_ATTENDANCE)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_ATTENDANCE}
                                            icon={localAssets.attendence}
                                            title={str('attendance')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_SIGNALS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_SIGNALS}
                                            icon={localAssets.signals}
                                            title={str('signals')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_EXPERT_ADVISORS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_EXPERT_ADVISORS}
                                            icon={localAssets.expertAdvise}
                                            title={str('expertAdvisors')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_INDICATORS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_INDICATORS}
                                            icon={localAssets.indicator}
                                            title={str('indicators')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_UTILITIES)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_UTILITIES}
                                            icon={localAssets.utility}
                                            title={str('utilities')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_SCRIPTS)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_SCRIPTS}
                                            icon={localAssets.script}
                                            title={str('scripts')} />
                                        <SideMenuItem
                                            onItemPress={() => navigateToScreen(ROUTE_LIBRARIES)}
                                            activeScreen={activeScreen}
                                            currentItem={ROUTE_LIBRARIES}
                                            icon={localAssets.library}
                                            title={str('libraries')} />
                                        <SideMenuItem
                                            onPressEn={() => updateLanguage('en')}
                                            // onPressZh={() => openPopup()}
                                            onPressZh={() => updateLanguage('zh')}
                                            language={appLanguage}
                                            icon={localAssets.language}
                                            title={str('language')} />
                                        <SideMenuItem
                                            onItemPress={() => logout()}
                                            icon={localAssets.power}
                                            title={str('logout')} />
                                    </View>
                                </ScrollView>
                            </View>
                        </Animated.View>
                    </View>
                    :
                    null
                }

                <View style={landingScreenStyle.bodyContainer}>
                    {renderScreen()}
                </View>
            </View>
        </View>
    );
};

export default LandingScreen;
