import { Pressable, Text } from 'react-native';
import { RegularButtonStyle } from '../RegularButton.style';

const RegularButton = (props: any) => {
  const {
    onPress,
    title,
    border,
  } = props;
  return (
    <Pressable
      onPress={onPress}
      style={border ?
        RegularButtonStyle.buttonBorder :
        RegularButtonStyle.button
      }>
      <Text
        style={border ?
          RegularButtonStyle.buttonTextBorder :
          RegularButtonStyle.buttonText
        }>
        {title}
      </Text>
    </Pressable>
  );
};
export default RegularButton;
