import * as React from 'react';
import {
    Image,
    Pressable,
    Text,
    View,
} from 'react-native';
import { useDeviceSize } from 'rn-responsive-styles';
import { HeaderStyle } from '../Header.style';

const Header = (props: any) => {

    const deviceSize = useDeviceSize();
    const headerStyle = HeaderStyle();

    const {
        styleMain,
        styleLeftPressable,
        styleLeftImage,
        styleRightImage,
        styleCenterImage,
        styleCenterText,
        sourceLeftImage,
        sourceRightImage,
        sourceCenterImage,
        centerText,
        onPressLeft,
        onPressRight,
        onPressCenter,
    } = props;

    return (
        <View style={styleMain || headerStyle.main}>
            {sourceLeftImage &&
                <Pressable onPress={onPressLeft}
                    style={styleLeftPressable}>
                    <Image
                        source={sourceLeftImage}
                        style={styleLeftImage || headerStyle.icon}
                        resizeMode={'contain'}
                    />
                </Pressable>
            }

            {sourceCenterImage ?
                deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                    <Pressable onPress={onPressCenter}>
                        <Image
                            source={sourceCenterImage}
                            style={styleCenterImage || headerStyle.logo}
                            resizeMode={'contain'}
                        />
                    </Pressable>
                    :
                    null
                :
                centerText &&
                    deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                    <Pressable onPress={onPressCenter}>
                        <Text style={styleCenterText}>
                            {centerText}
                        </Text>
                    </Pressable>
                    :
                    null
            }

            {sourceRightImage &&
                deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                <Pressable onPress={onPressRight}>
                    <Image
                        source={sourceRightImage}
                        style={styleRightImage || headerStyle.icon}
                        resizeMode={'contain'}
                    />
                </Pressable>
                :
                null
            }
        </View>
    );
};

export default Header;
