import { FlatList, Image, Text, View, Pressable, ScrollView } from 'react-native';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import translations from '../../resources/locales/translations';
import { localAssets } from '../../resources/assets/assets';
import { MyClassesStyle } from './MyClasses.style';

const MyClassesScreen = () => {

    // Start manage localization
    const { appLanguage } = useSelector((state: any) => state.Appreducer);
    const [language, setLanguage] = useState<'en' | 'zh'>('en');
    useEffect(() => {
        setLanguage(appLanguage);
    }, []);
    const str = (key: keyof typeof translations.en): string => {
        return translations[language][key];
    };
    // End manage localization
    const data = [
        {
            'id': '1',
            'classesName': 'English Speacking',
            'category': 'English',
            'type': 'Student',
            'signature': 'image',
            'slip': 'slipImg',
        },
        {
            'id': '2',
            'classesName': 'English Speacking',
            'category': 'English',
            'type': 'Student',
            'signature': 'image',
            'slip': 'slipImg',
        },
        {
            'id': '3',
            'classesName': 'English Speacking',
            'category': 'English',
            'type': 'Student',
            'signature': 'image',
            'slip': 'slipImg',
        },
        {
            'id': '4',
            'classesName': 'English Speacking',
            'category': 'English',
            'type': 'Student',
            'signature': 'image',
            'slip': '',
        },
    ];

    return (
        <View
            style={MyClassesStyle.mainOuter}
        >
            <ScrollView
                scrollEnabled={true}
                showsVerticalScrollIndicator={false}
                style={MyClassesStyle.mainScroll}
            >
                <View style={MyClassesStyle.mainView}>
                    <FlatList
                        scrollEnabled
                        nestedScrollEnabled
                        data={data}
                        renderItem={({ item }) => {
                            return (
                                <View
                                    style={MyClassesStyle.itemStyle}
                                >
                                    <Text style={MyClassesStyle.listTitleText}>
                                        {str('classesName')}
                                    </Text>
                                    <Text style={MyClassesStyle.listValueText}>
                                        {item.classesName}
                                    </Text>
                                    <View style={MyClassesStyle.horizontalLine} />

                                    {/* Second Section */}
                                    <View style={MyClassesStyle.listStyle}>
                                        <View
                                            style={MyClassesStyle.mainContainer}
                                        >
                                            <Text
                                                style={MyClassesStyle.listTitleText}
                                            >
                                                {str('category')}
                                            </Text>
                                            <Text style={MyClassesStyle.listValueText}>{item.category}</Text>
                                        </View>
                                        <View style={MyClassesStyle.verticleLine} />
                                        <View
                                            style={MyClassesStyle.mainContainer}
                                        >
                                            <Text
                                                style={MyClassesStyle.listTitleText}
                                            >
                                                {str('type')}
                                            </Text>
                                            <Text style={MyClassesStyle.listValueText}>{item.type}</Text>
                                        </View>
                                    </View>

                                    {/* Third Section */}
                                    <View style={MyClassesStyle.horizontalLine} />
                                    <View style={MyClassesStyle.listStyle}>
                                        <View
                                            style={MyClassesStyle.mainContainer}
                                        >
                                            <Text
                                                style={MyClassesStyle.listTitleText}
                                            >
                                                {str('signature')}
                                            </Text>
                                            <Image
                                                source={localAssets.dummysign}
                                                style={MyClassesStyle.signImage}
                                                resizeMode={'contain'}
                                            />
                                        </View>
                                        <View style={MyClassesStyle.verticleLine} />
                                        <View
                                            style={MyClassesStyle.mainContainer}
                                        >
                                            <Text
                                                style={MyClassesStyle.listTitleText}
                                            >
                                                {str('slip')}
                                            </Text>
                                            {item.slip === '' ?
                                                <Pressable>
                                                    <Text
                                                        style={MyClassesStyle.uploadText}
                                                    >
                                                        {str('uploadSlip')}
                                                    </Text>
                                                </Pressable>
                                                :
                                                <Image
                                                    source={localAssets.dummyImage}
                                                    style={MyClassesStyle.slipImage}
                                                    resizeMode={'contain'}
                                                />
                                            }
                                        </View>
                                    </View>
                                </View>
                            );
                        }}
                    />
                </View>
            </ScrollView>
        </View>
    );
};

export default MyClassesScreen;
