/* eslint-disable prettier/prettier */
import { Dimensions, Platform, StyleSheet } from 'react-native';
import { primaryBottom, white } from '../../resources/values/color';
import { Responsive } from '../../utils/Responsive';

const CustomToastStyle = StyleSheet.create({
  container: {
    height:
      Platform.OS === 'web'
        ? Dimensions.get('window').height - Responsive.size50 / 2.5
        : Dimensions.get('window').height - Responsive.size12,
    width: Dimensions.get('window').width,
    position: 'absolute',
    alignSelf: 'center',
    marginTop:
      Platform.OS === 'web' ? Responsive.size50 / 2.5 : Responsive.size12,
  },
  text: {
    fontWeight: '400',
    position: 'absolute',
    borderRadius:
      Platform.OS === 'web' ? Responsive.size12 / 2.5 : Responsive.size12,
    padding:
      Platform.OS === 'web' ? Responsive.size12 / 2.5 : Responsive.size12,
    backgroundColor: white,
    alignSelf: 'center',
    color: primaryBottom,
    fontSize:
      Platform.OS === 'web' ? Responsive.size16 / 2.5 : Responsive.size16,
    lineHeight:
      Platform.OS === 'web' ? Responsive.size20 / 2.5 : Responsive.size20,
  },
});

export { CustomToastStyle };
