import { GradientStyle } from '../Gradient.style';
import Svg, { Defs, LinearGradient, Rect, Stop } from 'react-native-svg';
import { primaryBottom, primaryTop } from '../../../resources/values/color';
import { View, StatusBar } from 'react-native';

const Gradient = () => {
    return (
        <View style={GradientStyle.container}>
            <StatusBar hidden={true} />
            <Svg height="100%" width="100%">
                <Defs>
                    <LinearGradient id="gradient" x1="0%" y1="0%" x2="0%" y2="100%">
                        <Stop offset="0%" stopColor={primaryTop} />
                        <Stop offset="100%" stopColor={primaryBottom} />
                    </LinearGradient>
                </Defs>
                <Rect x="0" y="0" width="100%" height="100%" fill="url(#gradient)" />
            </Svg>
        </View>
    );
};

export default Gradient;
