import { Platform, Text, View } from 'react-native';
import moment from 'moment';
import React from 'react';
import { Responsive } from './Responsive';
import Fonts from './Fonts';
import { error, green, warning, white } from '../resources/values/color';

export const showToast = async (message: string, toast: any, type?: any) => {
  const backgroundColor =
    type === 'danger' ? error : type === 'warning' ? warning : green;

  await cloase(toast);
  toast.show({
    placement: 'top',
    duration: 2400,
    render: () => {
      return (
        <View style={{
          paddingHorizontal: Responsive.size20,
          paddingVertical: Responsive.size10,
          backgroundColor,
          borderRadius: Responsive.size10,
        }}>
          <Text style={{
            fontFamily: Fonts.medium,
            fontSize: Responsive.size12,
            lineHeight: Responsive.size18,
            color: white,
          }}>{message}</Text>
        </View>
      );
    },
  });
};
export const showDangerToast = (message: string, toast: any) => {
  showToast(message, toast, 'danger');
};
export const showWarningToast = (message: string, toast: any) => {
  showToast(message, toast, 'warning');
};
export const showSuccessToast = (message: string, toast: any) => {
  showToast(message, toast,);
};
const cloase = (toast: any) => {
  return toast.closeAll();
};

export const isValidEmail = (email: any) => {
  let reg =
    /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
  return reg.test(email) === true && email.length <= 40;
};

export const isValidMobile = (mobile: any) => {
  const omanMobileRegex = /^(968)?[2-9]\d{7}$/;
  // const omanMobileRegex = /^(?:\+968|968)?[49]\d{7}$/;
  return omanMobileRegex.test(mobile);
};

export const formatDate = (date: any, format = 'DD-MM-YYYY HH:mm', milis = false) => {
  const newDate = !milis ? date * 1000 : date;
  return moment(newDate).format(format);
};

export const isValidPassword = (password: any) => {
  let length = password.length;
  return length >= 6 && length <= 20;
};
export const isEmpty = (value: any) => {
  return !value || value.toString().trim().length <= 0;
};

export const getDeviceType = () => {
  return isIos() ? 'I' : 'A';
};
export const isIos = () => {
  return Platform.OS === 'ios';
};
export const EMAIL_REGEX = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
