/* eslint-disable react/react-in-jsx-scope */

import { StatusBar, View } from 'react-native';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import translations from '../../resources/locales/translations';
import { localAssets } from '../../resources/assets/assets';
import { primaryTop } from '../../resources/values/color';
import { StatusbarStyle } from '../../utils/statusbarStyle';
import Header from '../common/ui/Header';
import { navigate } from '../../routes/Navigator';
import { ROUTE_MOBILE_SIDE_MENU } from '../../routes/RouteConst';
import ToggleButton from '../common/ui/ToggleButton';
import MyClassesScreen from './MyClasses';
import { ClassesStyle } from './Classes.style';
import AllClasses from './AllClasses';

const ClassesScreen = () => {

  const [selectedTab, setSelectedTab] = useState('My Classes');

  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  return (
    <View
      style={ClassesStyle.mainView}
    >
      <StatusBar backgroundColor={primaryTop} barStyle={StatusbarStyle.LIGHT} hidden={false} />
      <View style={ClassesStyle.mobileOuter}>
        <Header
          styleMain={ClassesStyle.mainToolBar}
          sourceLeftImage={localAssets.list}
          onPressLeft={() => {
            navigate(ROUTE_MOBILE_SIDE_MENU);
          }}
          styleCenterText={ClassesStyle.centerTextHeader}
          centerText={str('classes')}
        />
      </View>
      <View style={ClassesStyle.toggleOuter}>
        <ToggleButton
          firstTitle={str('myClasses')}
          secondTitle={str('allClasses')}
          onToggleFirst={() => {
            setSelectedTab('My Classes');
          }}
          onToggleOnSecond={() => {
            setSelectedTab('All Classes');
          }}
        />
      </View>
      {selectedTab === 'My Classes' ? <MyClassesScreen /> : <AllClasses />}
    </View>
  );
};

export default ClassesScreen;
