/* eslint-disable prettier/prettier */
import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  email: '',
  errorEmail: '',
  password: '',
  errorPassword: '',
  rememberMe: false,
  loading: false,
};

export const signInReducer = createSlice({
  name: 'signInReducer',
  initialState,
  reducers: {
    addEmail: (state, action) => {
      state.email = action.payload;
    },
    addErrorEmail: (state, action) => {
      state.errorEmail = action.payload;
    },
    addPassword: (state, action) => {
      state.password = action.payload;
    },
    addErrorPassword: (state, action) => {
      state.errorPassword = action.payload;
    },
    addRememberMe: (state, action) => {
      state.rememberMe = action.payload;
    },
    loginLoader: (state, action) => {
      state.loading = action.payload;
    },
    clearState: () => {
      return initialState;
    },
  },
});

export const { addEmail, addErrorEmail, addPassword, addErrorPassword, addRememberMe, loginLoader, clearState } = signInReducer.actions;

export default signInReducer.reducer;
