import * as React from 'react';
import {
    Image,
    Pressable,
    Text,
    View,
} from 'react-native';
import { SideMenuItemStyle } from '../SideMenuItem.style';

const SideMenuItem = (props: any) => {

    const sideMenuItemStyle = SideMenuItemStyle();

    const {
        isMobile,
        onItemPress,
        activeScreen,
        currentItem,
        icon,
        title,
        language,
        onPressEn,
        onPressZh,
    } = props;

    return (
        <View style={isMobile ? [sideMenuItemStyle.outerMobile, title === 'Language' && sideMenuItemStyle.outerLanguage, title === '' && sideMenuItemStyle.outerLanguage] : [sideMenuItemStyle.outerWeb, title === 'Language' && sideMenuItemStyle.outerLanguage, title === '' && sideMenuItemStyle.outerLanguage]}>
            <Pressable
                onPress={onItemPress}
                style={
                    isMobile ?
                        sideMenuItemStyle.itemContainer
                        :
                        activeScreen ?
                            activeScreen === currentItem ?
                                sideMenuItemStyle.activeItem :
                                sideMenuItemStyle.inActiveItem
                            :
                            sideMenuItemStyle.inActiveItem
                }>
                {
                    activeScreen &&
                    activeScreen === currentItem &&
                    <View style={sideMenuItemStyle.activeIndicator} />
                }
                <Image
                    source={icon}
                    style={isMobile ? sideMenuItemStyle.itemIcon : sideMenuItemStyle.icon}
                    resizeMode="center" />
                <Text style={isMobile ? sideMenuItemStyle.itemTitle : sideMenuItemStyle.title}>{title}</Text>
            </Pressable>

            {
                title === 'Language' || title === '' ?
                    <View style={isMobile ? sideMenuItemStyle.switchStyle : sideMenuItemStyle.switchStyleWeb}>
                        <Pressable
                            disabled={language === 'en'}
                            onPress={onPressEn}
                            style={[isMobile ? sideMenuItemStyle.switchLeft : sideMenuItemStyle.switchLeftWeb, language === 'en' && sideMenuItemStyle.switchActive]}>
                            <Text style={[isMobile ? sideMenuItemStyle.switchText : sideMenuItemStyle.switchTextWeb, language === 'en' ? sideMenuItemStyle.switchTextActive : sideMenuItemStyle.switchTextInActive]}>EN</Text>
                        </Pressable>
                        <Pressable
                            disabled={language === 'zh'}
                            onPress={onPressZh}
                            style={[isMobile ? sideMenuItemStyle.switchRight : sideMenuItemStyle.switchRightWeb, language === 'zh' && sideMenuItemStyle.switchActive]}>
                            <Text style={[isMobile ? sideMenuItemStyle.switchText : sideMenuItemStyle.switchTextWeb, language === 'zh' ? sideMenuItemStyle.switchTextActive : sideMenuItemStyle.switchTextInActive]}></Text>
                        </Pressable>
                    </View>
                    :
                    null
            }
        </View>
    );
};

export default SideMenuItem;
