/* eslint-disable prettier/prettier */
import { StyleSheet } from 'react-native';
import { Responsive } from '../../utils/Responsive';
import { latestItemSeprator } from '../../resources/values/color';

const SeparatorStyle = (deviceSize : String) =>
  StyleSheet.create({
    dashLine: {
        borderWidth: Responsive.size1,
        borderColor: latestItemSeprator,
        marginVertical:
          deviceSize === 'extralarge' || deviceSize === 'large'
            ? Responsive.size16 / 2.5
            : Responsive.size16,
        borderStyle: 'dashed',
      },
  });

export { SeparatorStyle };
