/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
import {
  Image,
  SafeAreaView,
  ScrollView,
  StatusBar,
  Text,
  View,
} from 'react-native';
import { primaryTop } from '../../resources/values/color';
import { StatusbarStyle } from '../../utils/statusbarStyle';
import { SideMenuScreenStyle } from './SideMenuScreen.style';
import { localAssets } from '../../resources/assets/assets';
import React, { useEffect, useState } from 'react';
import { reset } from '../../routes/Navigator';
import {
  ROUTE_ACCOUNT,
  ROUTE_ACTIVITIES,
  ROUTE_ANNOUNCEMENTS,
  ROUTE_ATTENDANCE,
  ROUTE_CLASSES,
  ROUTE_COMING_SOON,
  ROUTE_DASHBOARD,
  ROUTE_EXPERT_ADVISORS,
  ROUTE_GIFTS,
  ROUTE_INDICATORS,
  ROUTE_LANDING,
  ROUTE_LIBRARIES,
  ROUTE_POINTS,
  ROUTE_SCRIPTS,
  ROUTE_SIGNALS,
  ROUTE_SIGNIN_SCREEN,
  ROUTE_TICKETS,
  ROUTE_UTILITIES,
  ROUTE_VPS_MACHINES,
} from '../../routes/RouteConst';
import AlertDialog from '../common/ui/AlertDialog';
import { store } from '../../services/redux/store';
import { loggedOut, setAppLanguage } from '../../services/app/Appreducer';
import { addPassword } from '../Authentication/SignIn/signin.reducer';
import { useDispatch, useSelector } from 'react-redux';
import Header from '../common/ui/Header';
import SideMenuItem from '../common/ui/SideMenuItem';
import translations from '../../resources/locales/translations';

export const SideMenuScreen = (props: any) => {
  const dispatch = useDispatch();

  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  const userDetails = useSelector(
    (state: any) => state?.Appreducer?.userDetails
  );

  useEffect(() => {
    console.log(props.route.params, 'props1');
  }, []);

  // For the Navigation
  const navigateToScreen = (screen: any) => {
    switch (screen) {
      case ROUTE_DASHBOARD:
        return reset(ROUTE_LANDING, ROUTE_DASHBOARD);
      case ROUTE_POINTS:
        return reset(ROUTE_POINTS, ROUTE_DASHBOARD);
      case ROUTE_CLASSES:
        return reset(ROUTE_CLASSES, ROUTE_DASHBOARD);
      case ROUTE_GIFTS:
        return reset(ROUTE_GIFTS, ROUTE_DASHBOARD);
      default:
        return reset(ROUTE_LANDING, ROUTE_COMING_SOON);
    }
  };

  // To close the sidebar
  const closeDrawer = () => {
    navigateToScreen(props.route.params);
  };

  // To show the alert dialog for the logout
  async function logout() {
    AlertDialog.show({
      title: str('logout'),
      message: str('confirmLogout'),
      positiveButton: {
        title: str('yes'),
        onPress: async () => {
          store.dispatch(addPassword(''));
          store.dispatch(loggedOut());
          reset(ROUTE_SIGNIN_SCREEN);
          AlertDialog.hide();
        },
      },
      negativeButton: {
        title: str('no'),
        onPress: async () => {
          AlertDialog.hide();
        },
      },
    });
  }

  const updateLanguage = (lan: string) => {
    dispatch(setAppLanguage(lan));
  };

  return (
    <View style={SideMenuScreenStyle.mainOuter}>
      <StatusBar
        backgroundColor={primaryTop}
        barStyle={StatusbarStyle.LIGHT}
        hidden={false}
      />
      <SafeAreaView style={SideMenuScreenStyle.fullFlex}>
        <View style={SideMenuScreenStyle.fullFlex}>
          <Header
            sourceLeftImage={localAssets.dismiss}
            sourceRightImage={localAssets.power}
            onPressLeft={() => closeDrawer()}
            onPressRight={() => logout()}
          />
          {/*Show the user details*/}
          <View style={SideMenuScreenStyle.sideMenuProfileDetails}>
            <Image
              source={
                userDetails?.avatar
                  ? { uri: userDetails?.avatar }
                  : localAssets.profile
              }
              style={SideMenuScreenStyle.sideMenuImageStyle}
              resizeMode={'contain'}
            />
            <View style={SideMenuScreenStyle.sideMenuTextContentStyle}>
              <Text style={SideMenuScreenStyle.sideMenuTitleStyle}>
                {language === 'en' ? userDetails?.englishFirstName : userDetails?.chineseFirstName} {language === 'en' ? userDetails?.englishLastName : userDetails?.chineseLastName}
              </Text>
              <Text style={SideMenuScreenStyle.sideMenuSubTitleStyle}>
                {userDetails?.email}
              </Text>
            </View>
          </View>
          <View style={SideMenuScreenStyle.sideMenuContainer}>
            <ScrollView showsVerticalScrollIndicator={false}>
              <View>
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_DASHBOARD)}
                  icon={localAssets.home}
                  title={str('dashboard')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_POINTS)}
                  icon={localAssets.points}
                  title={str('Points')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_GIFTS)}
                  icon={localAssets.gift}
                  title={str('gifts')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_ANNOUNCEMENTS)}
                  icon={localAssets.announcement}
                  title={str('announcements')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_ACCOUNT)}
                  icon={localAssets.account}
                  title={str('account')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_TICKETS)}
                  icon={localAssets.ticket}
                  title={str('tickets')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_VPS_MACHINES)}
                  icon={localAssets.vps}
                  title={str('vpsMachines')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_CLASSES)}
                  icon={localAssets.class}
                  title={str('classes')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_ACTIVITIES)}
                  icon={localAssets.activity}
                  title={str('activities')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_ATTENDANCE)}
                  icon={localAssets.attendence}
                  title={str('attendance')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_SIGNALS)}
                  icon={localAssets.signals}
                  title={str('signals')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_EXPERT_ADVISORS)}
                  icon={localAssets.expertAdvise}
                  title={str('expertAdvisors')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_INDICATORS)}
                  icon={localAssets.indicator}
                  title={str('indicators')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_UTILITIES)}
                  icon={localAssets.utility}
                  title={str('utilities')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_SCRIPTS)}
                  icon={localAssets.script}
                  title={str('scripts')}
                />
                <SideMenuItem
                  isMobile={true}
                  onItemPress={() => navigateToScreen(ROUTE_LIBRARIES)}
                  icon={localAssets.library}
                  title={str('libraries')}
                />
                <SideMenuItem
                  isMobile={true}
                  icon={localAssets.language}
                  title={str('language')}
                  language={appLanguage}
                  onPressEn={() => updateLanguage('en')}
                  // onPressZh={() => openPopup()}
                onPressZh={() => updateLanguage('zh')}
                />
              </View>
            </ScrollView>
          </View>
        </View>
      </SafeAreaView>
    </View>
  );
};

export default SideMenuScreen;
