/* eslint-disable prettier/prettier */
import React, { useState } from 'react';
import {
  View,
  Text,
  Animated,
  Easing,
  Pressable,
} from 'react-native';
import { ToggleButtonStyle } from '../ToggleButton.style';


const ToggleButton = ({
  firstTitle,
  secondTitle,
  onToggleFirst,
  onToggleOnSecond,
}: {
  firstTitle: String;
  secondTitle: String;
  onToggleFirst: any;
  onToggleOnSecond: any;
}) => {
  const animatedValue = React.useRef(new Animated.Value(0)).current;
  const [isToggled, setIsToggled] = useState(0);
  const startAnimation = (toValue: any) => {
    setIsToggled(toValue);
    Animated.timing(animatedValue, {
      toValue,
      duration: 250,
      easing: Easing.linear,
      useNativeDriver: false,
    }).start();
    if (toValue === 0) {
      onToggleFirst(); // Trigger the off toggle action
    } else {
      onToggleOnSecond(); // Trigger the on toggle action
    }
  };
  const left = animatedValue.interpolate({
    inputRange: [0, 1],
    outputRange: ['0%', '50%'],
    extrapolate: 'clamp',
  });

  return (
    <View style={ToggleButtonStyle.container}>
      <Animated.View style={[ToggleButtonStyle.slider, { left }]} />
      <Pressable
        style={ToggleButtonStyle.clickableArea}
        onPress={startAnimation.bind(null, 0)}
      >
        <Text
          style={[isToggled === 1 ? ToggleButtonStyle.sliderText : ToggleButtonStyle.unSelectText]}
        >
          {firstTitle}
        </Text>
      </Pressable>
      <Pressable
        style={ToggleButtonStyle.clickableArea}
        onPress={startAnimation.bind(null, 1)}
      >
        <Text
          style={[isToggled === 0 ? ToggleButtonStyle.sliderText : ToggleButtonStyle.unSelectText]}
        >
          {secondTitle}
        </Text>
      </Pressable>
    </View>
  );
};



export default ToggleButton;
