import { View, TouchableOpacity, Text } from 'react-native';
import { CustomTabBarStyle } from '../CustomTabBar.style';
import { tabBar } from '../../../resources/values/color';
import { Responsive } from '../../../utils/Responsive';


const CustomTabBar = ({ tabs, activeTab, onTabPress }: { tabs: any, activeTab: any, onTabPress: any }) => {
    return (
        <>
            <View style={CustomTabBarStyle.tabBar}>
                {tabs.map((tab: string | number | boolean | React.ReactElement<any, string | React.JSXElementConstructor<any>> | Iterable<React.ReactNode> | React.ReactPortal | null | undefined, index: React.Key | null | undefined) => (
                    <TouchableOpacity
                        key={index}
                        onPress={() => onTabPress(index)}
                        style={[
                            CustomTabBarStyle.tabItem,
                            { borderBottomWidth: activeTab === index ? Responsive.size4 / 2 : 0, borderColor: tabBar },
                        ]}
                    >
                        <Text style={activeTab === index ? CustomTabBarStyle.activeTabText : CustomTabBarStyle.inActiveTabText}>{tab}</Text>

                    </TouchableOpacity>
                ))}
            </View>
            <View style={CustomTabBarStyle.bottomLineStyle} />
        </>
    );
};

