/* eslint-disable prettier/prettier */
import NetInfo from '@react-native-community/netinfo';
import {default as axios} from 'axios';
import {METHOD} from './ApiTypes';
import {reset} from '../../routes/Navigator';
import * as DialogManager from '../../components/common/ui/DialogManager';
import {BASE_URL} from './ApiConstants';
import {store} from '../redux/store';
import { loggedOut } from '../app/Appreducer';

export default async (
  endpoint: string,
  params = {},
  onSuccess: any,
  onFailure: any,
  method: string,
  base?: string,
) => {

  const {appLanguage, token} = store.getState().Appreducer;

  console.log('---------------------------------------------');
  console.log('---------------------------------------------\n');

  const isConnected = await NetInfo.fetch();

  const headers = {
    Authorization:  token ? `Bearer ${token}` : '' ,
    'Accept-Language': appLanguage,
  };

  if (!isConnected.isConnected) {
    onFailure('Please check your internet connection.');
  } else {
    const configs = {
      baseURL: base || BASE_URL,
      params,
      timeout: 60000,
      headers,
    };
    let request: any;
    switch (method) {
      case METHOD.POST:
        request = axios.post(endpoint, params, configs);
        break;
      case METHOD.GET:
        request = axios.get(endpoint, configs);
        break;
      case METHOD.DEL:
        request = axios.delete(endpoint, configs);
        break;
      case METHOD.PUT:
        request = axios.put(endpoint, params, configs);
        break;
      case METHOD.PATCH:
        request = axios.patch(endpoint, params, configs);
        break;
    }

    console.log(
      base || BASE_URL + endpoint + '\n------------------Params-------------------',
    );
    console.log(token);
    console.log(JSON.stringify(params));
    console.log(JSON.stringify(headers));
    console.log('---------------------------------------------');
    request
      .then((response: {status: number; data: {error: any[]}; message: null}) => {
        console.log(
          `--------------- Response ${response.status} ---------------- `,
        );
        console.log(endpoint + '\n Response', JSON.stringify(response.data));

        switch (response.status) {
          case 200:
            onSuccess(response.data ? response.data : response.data);
            break;
          case 201:
            onSuccess(response.data ? response.data : response.data);
            break;
          case 204:
            onSuccess(response.data ? response.data : response.data);
            break;
          case 400:
            if (response.message !== null) {
              onSuccess(response.message);
            } else {
              const error = response.data.error[0];
              onSuccess(
                error && typeof error === 'string'
                  ? error
                  : 'Something went wrong',
              );
            }
        }
      })
      .catch(
        (error: {
          response?: {
            data: {message?: string};
            status?: number;
            code?: string;
          };
        }) => {
          console.log(
            endpoint + '\n Error',
            JSON.stringify(error.response?.data),
          );
          if (error?.response?.data) {
            if (error?.response?.status === 401) {
              if (error?.response?.code === 'INVALID_TOKEN') {
                DialogManager.alertOneButton(
                  'Session expired',
                  'Session is expired. Need to re-login',
                  'Relogin',
                  () => {
                    onFailure('Session expired');
                    reset('LoginPage');
                    store.dispatch(loggedOut());
                  },
                );
              } else {
                onFailure(error?.response?.data?.message);
              }
            } else {
              onFailure(error?.response?.data?.message);
            }
          } else {
            onFailure(error.toString());
          }
        },
      );
  }
};
