/* eslint-disable prettier/prettier */
import { combineReducers } from '@reduxjs/toolkit';
import counterReducer from '../app/Appreducer';
import Appreducer from '../app/Appreducer';
import DashboardReducer from '../../components/Dashboard/dashboard.reducer';
import signInReducer from '../../components/Authentication/SignIn/signin.reducer';

const appReducer = combineReducers({
  // Export Files from Reducers
  counterReducer,
  Appreducer,
  signInReducer,
  DashboardReducer,
});

const rootReducer = (state: any, action: any) => {
  return appReducer(state, action);
};

export default rootReducer;
