/* eslint-disable react/react-in-jsx-scope */
import { ScrollView, View } from 'react-native';
import { useDispatch } from 'react-redux';
import { useEffect, useState } from 'react';
import { getApi } from '../../services/API/Api-Helper';
import { API_EA_UPDATES_LIST, API_PROFILE } from '../../services/API/ApiConstants';
import { setUserDetails } from '../../services/app/Appreducer';
import { EAUpdatesList } from './dashboard.reducer';
import { useDeviceSize } from 'rn-responsive-styles';
import VPSListScreen from '../VPS/VPSListScreen';
import CustomToast from '../common/ui/CustomToast';
import CustomLoader from '../common/ui/CustomLoader';
import { DashboardScreenStyle } from './DashboardScreen.style';
import AttendanceScreen from '../Attendance/AttendanceScreen';
import AccountScreen from '../Account/AccountScreen';
import EAProductScreen from '../LatestUpdate/EAProductScreen';

export const Dashboard = () => {
    const deviceSize = useDeviceSize();

    const dashboardStyle = DashboardScreenStyle(deviceSize);

    const [toastVisible, setToastVisible] = useState(false);
    const [toastMessage, setToastMessage] = useState('');
    const [loading, setLoading] = useState(false);
    // Show the toast message
    const showToast = (message: any) => {
        console.log(message);
        setToastMessage(message);
        setToastVisible(true);
        setTimeout(() => {
            setToastVisible(false);
        }, 3000);
    };

    const dispatch = useDispatch();
    useEffect(() => {
        getUserDetails();
        // getEAUpdatesList();
    }, []);

    // Function for call get User Details API
    const getUserDetails = () => {
        let params = {
        };
        setLoading(true);
        getApi(API_PROFILE, params, callBackSuccess, callBackError);
    };
    // Handle API success callback
    const callBackSuccess = (response: any) => {
        dispatch(setUserDetails(response.data));
        getEAUpdatesList();
    };
    // Handle API failure callback
    const callBackError = (error: String) => {
        showToast(error);
        setLoading(false);
    };

    // Function for call get EA Updates List API
    const getEAUpdatesList = () => {
        let params = {
        };
        getApi(API_EA_UPDATES_LIST, params, callBackSuccessEA, callBackErrorEA);
    };
    // Handle API success callback
    const callBackSuccessEA = (response: any) => {
        setLoading(false);
        dispatch(EAUpdatesList(response.data));
    };
    // Handle API failure callback
    const callBackErrorEA = (error: String) => {
        setLoading(false);
        showToast(error);
    };

    return (
        <ScrollView
            scrollEnabled={true}
            showsVerticalScrollIndicator={false}
            style={dashboardStyle.mainScroll}>
            <View style={dashboardStyle.mainDirection}>
                <View style={deviceSize === 'extralarge' || deviceSize === 'large' ?
                    dashboardStyle.mainOuterFlex
                    :
                    dashboardStyle.mainOuter
                }>
                    <AttendanceScreen />
                </View>
                {deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                    <View style={dashboardStyle.seprator} />
                    :
                    null
                }
                {deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                    <View>
                        <VPSListScreen />
                    </View>
                    :
                    null
                }
                {deviceSize === 'medium' || deviceSize === 'small' || deviceSize === 'extrasmall' ?
                    <View style={dashboardStyle.seprator} />
                    :
                    null
                }
                <View style={deviceSize === 'extralarge' || deviceSize === 'large' ?
                    dashboardStyle.accountOuterWeb
                    :
                    dashboardStyle.accountOuter
                }>
                    <AccountScreen />
                </View>
                <View style={deviceSize === 'extralarge' || deviceSize === 'large' ?
                    dashboardStyle.eaOuterWeb
                    :
                    dashboardStyle.eaOuter
                }>
                    <EAProductScreen />
                </View>
            </View>
            {
                deviceSize === 'extralarge' || deviceSize === 'large' ?
                    <View style={dashboardStyle.vpsSeprator} />
                    :
                    null
            }
            {
                deviceSize === 'extralarge' || deviceSize === 'large' ?
                    <View>
                        <VPSListScreen />
                    </View>
                    :
                    null
            }
            {toastVisible && <CustomToast message={toastMessage} />}
            {loading && <CustomLoader />}
        </ScrollView >
    );
};

export default Dashboard;
