/* eslint-disable prettier/prettier */
import { createSlice } from '@reduxjs/toolkit';
import { reset } from '../../routes/Navigator';
import { ROUTE_SPLASH_SCREEN } from '../../routes/RouteConst';

const initialState = {
  isLogged: 0,
  appLanguage: '',
  token: '',
  refreshToken:'',
  deviceId: '',
  firstLoad: 1,
  userDetails: '',
};

// Function for reset the screen after change the language
const reloadAfterUpdate = async () => {
  reset(ROUTE_SPLASH_SCREEN);
};

export const AppReducer = createSlice({
  name: 'AppReducer',
  initialState,
  reducers: {
    loggedIn: state => {
      state.isLogged = 1;
    },
    loggedOut: state => {
      state.isLogged = 0;
    },
    setToken: (state, action) => {
      state.token = action.payload;
    },
    setRefreshToken: (state, action) => {
      state.refreshToken = action.payload;
  },
    setDeviceId: (state, action) => {
      state.deviceId = action.payload;
    },
    setFirstLoad: (state, action) => {
      state.firstLoad = action.payload;
    },
    setUserDetails: (state, action) => {
      state.userDetails = action.payload;
    },
    setAppLanguage: (state, action) => {
      state.appLanguage = action.payload;
      // setTimeout(() => {
        reloadAfterUpdate();
      // }, 100);
    },
  },
});

// Action creators are generated for each case reducer function
export const {
  setFirstLoad,
  setToken,
  setRefreshToken,
  setDeviceId,
  loggedIn,
  loggedOut,
  setUserDetails,
  setAppLanguage,
} = AppReducer.actions;

export default AppReducer.reducer;
