/* eslint-disable prettier/prettier */
import { NavigationContainer } from '@react-navigation/native';
import { TransitionPresets, createStackNavigator } from '@react-navigation/stack';
import React from 'react';
import { navigationRef } from './Navigator';

import { ROUTE_CLASSES, ROUTE_COMING_SOON, ROUTE_DASHBOARD, ROUTE_GIFTS, ROUTE_LANDING, ROUTE_MOBILE_SIDE_MENU, ROUTE_POINTS, ROUTE_REGISTRATION, ROUTE_SIGNIN_SCREEN, ROUTE_SPLASH_SCREEN } from './RouteConst';
import SplashScreen from '../components/Splash/SplashScreen';
import SignInScreen from '../components/Authentication/SignIn/SignInScreen';
import SideMenuScreen from '../components/SideMenu/SideMenuScreen';
import ComingSoonScreen from '../components/common/ui/ComingSoonScreen';
import Dashboard from '../components/Dashboard/DashboardScreen';
import LandingScreen from '../components/Landing/LandingScreen';
import ClassesScreen from '../components/Classes/ClassesScreen';
import RegistrationScreen from '../components/Registration/Registration';
import GiftsScreen from '../components/Gifts/Gifts';
import PointsScreen from '../components/Points/PointsScreen';

const Stack = createStackNavigator();

export default () => {
  return (
    <NavigationContainer ref={navigationRef}>
      <Stack.Navigator
        screenOptions={{ headerShown: false }}
        initialRouteName={ROUTE_SPLASH_SCREEN}
      >
        <Stack.Screen component={Dashboard} name={ROUTE_DASHBOARD} />
        <Stack.Screen component={SplashScreen} name={ROUTE_SPLASH_SCREEN} />
        <Stack.Screen component={SignInScreen} name={ROUTE_SIGNIN_SCREEN} />
        <Stack.Screen component={RegistrationScreen} name={ROUTE_REGISTRATION} />
        {/* <Stack.Screen component={WebSideMenuScreen} name={ROUTE_WEB_LANDING_PAGE} /> */}
        {/* <Stack.Screen component={DashboardScreen} name={ROUTE_HOME_PAGE}
          options={{ ...TransitionPresets.SlideFromRightIOS, gestureDirection: 'horizontal', gestureEnabled: true }}
        /> */}
        <Stack.Screen
          component={SideMenuScreen}
          name={ROUTE_MOBILE_SIDE_MENU}
          options={{
            ...TransitionPresets.SlideFromRightIOS,
            gestureDirection: 'horizontal-inverted',
            gestureEnabled: true,
          }}
        />

        <Stack.Screen
          component={ComingSoonScreen}
          name={ROUTE_COMING_SOON}
          options={{
            ...TransitionPresets.SlideFromRightIOS,
            gestureDirection: 'horizontal',
            gestureEnabled: true,
          }}
        />

        <Stack.Screen
          component={PointsScreen}
          name={ROUTE_POINTS}
          options={{
            ...TransitionPresets.SlideFromRightIOS,
            gestureDirection: 'horizontal',
            gestureEnabled: true,
          }}
        />

        <Stack.Screen
          component={ClassesScreen}
          name={ROUTE_CLASSES}
          options={{
            ...TransitionPresets.SlideFromRightIOS,
            gestureDirection: 'horizontal',
            gestureEnabled: true,
          }}
        />

        <Stack.Screen
          component={GiftsScreen}
          name={ROUTE_GIFTS}
          options={{
            ...TransitionPresets.SlideFromRightIOS,
            gestureDirection: 'horizontal',
            gestureEnabled: true,
          }}
        />

        <Stack.Screen
          component={LandingScreen}
          name={ROUTE_LANDING}
          options={{
            ...TransitionPresets.SlideFromRightIOS,
            gestureDirection: 'horizontal',
            gestureEnabled: true,
          }}
        />
      </Stack.Navigator>
    </NavigationContainer>
  );
};
