/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/no-unstable-nested-components */
import React, { useEffect, useState } from 'react';
import {
  FlatList,
  Image,
  SafeAreaView,
  Text,
  View,
} from 'react-native';
import { localAssets } from '../../resources/assets/assets';
import { useSelector } from 'react-redux';
import { useDeviceSize } from 'rn-responsive-styles';
import { EAProductScreenStyle } from './EAProductScreen.style';
import Separator from '../common/ui/Separator';
import RoundButton from '../common/ui/RoundButton';
import translations from '../../resources/locales/translations';

export const EAProductScreen = () => {
  // Start manage localization
  const { appLanguage } = useSelector((state: any) => state.Appreducer);
  const [language, setLanguage] = useState<'en' | 'zh'>('en');
  useEffect(() => {
    setLanguage(appLanguage);
  }, []);
  const str = (key: keyof typeof translations.en): string => {
    return translations[language][key];
  };
  // End manage localization

  // get the data of EA product
  const homeData = useSelector((state: any) => state.DashboardReducer);

  // To get the current device size
  const deviceSize = useDeviceSize();
  const latestUpdateStyle = EAProductScreenStyle(deviceSize);

  const ResponseComponent = ({ htmlString }: any) => {
    // Render the HTML text
    const pTags = htmlString.match(/<p>.*?<\/p>/g);

    if (pTags && pTags.length >= 2) {
      const firstPTagContent = pTags[0].replace(/<\/?p>/g, '');
      const secondPTagContent = pTags[1].replace(/<\/?p>/g, '');

      if (firstPTagContent.includes('<br>')) {
        return (
          <Text style={latestUpdateStyle.titleText}>
            {firstPTagContent.replace(/<\/?br>/g, '\n\n')}
          </Text>
        );
      } else {
        return (
          <Text style={latestUpdateStyle.titleText}>
            {firstPTagContent + '\n\n' + secondPTagContent}
          </Text>
        );
      }
    } else {
      return (
        <View>
          <Text style={latestUpdateStyle.titleText}>
            {htmlString.replace(/<\/?p>/g, '')}
          </Text>
        </View>
      );
    }
  };

  return (
    <SafeAreaView style={latestUpdateStyle.main}>
      <View style={latestUpdateStyle.containerView}>
        <Text style={latestUpdateStyle.subTitleText}>{str('eaLatestUpdate')}</Text>
        <RoundButton
          title={str('eaProduct')}
          size="small"
        />
      </View>
      {/* show the list of latest update product */}
      <FlatList
        style={latestUpdateStyle.listView}
        keyExtractor={(item, index) => String(index)}
        scrollEnabled
        nestedScrollEnabled
        data={homeData?.EAUpdatesList}
        renderItem={({ index, item }) => {
          const isEnd = index === homeData?.EAUpdatesList?.length - 1;
          const htmlContent = item?.ExpertAdvisorVersionList?.[0]?.info;
          return (
            <>
              <View style={latestUpdateStyle.imageView}>
                <Image
                  source={
                    index % 2 === 1
                      ? localAssets.book
                      : {
                        uri: 'https://gems-fx-prod.s3.amazonaws.com/production/user/106b6a8263cf332dd3a5-S10Adv.png',
                      }
                  }
                  style={latestUpdateStyle.listItemStyle}
                />
                <View style={latestUpdateStyle.fullScreen}>
                  <ResponseComponent htmlString={htmlContent} />
                </View>
              </View>
              {/* To show the dash line */}
              {!isEnd && <Separator />}
            </>
          );
        }}
      />
    </SafeAreaView>
  );
};
export default EAProductScreen;
