/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';

import { Image, StatusBar, View } from 'react-native';
import { useDispatch, useSelector } from 'react-redux';
import { localAssets } from '../../resources/assets/assets';
import { reset } from '../../routes/Navigator';
import { ROUTE_LANDING, ROUTE_SIGNIN_SCREEN } from '../../routes/RouteConst';
import GradientComponent from '../common/ui/Gradient';
import { splashScreenStyle } from './SplashScreen.style';
import { setAppLanguage } from '../../services/app/Appreducer';

const SplashScreen = () => {
  const dispatch = useDispatch();
  const data = useSelector((state: any) => state.Appreducer);

  // Navigate to the signin screen after 1.5 seconds
  useEffect(() => {

    if (data?.appLanguage === '') {
      dispatch(setAppLanguage('en'));
    }

    setTimeout(() => {
      if (data.isLogged) {
        reset(ROUTE_LANDING);
      } else {
        reset(ROUTE_SIGNIN_SCREEN);
      }
    }, 1500);
  }, []);

  return (
    <View style={splashScreenStyle.container}>
      <StatusBar hidden={true} />
      <GradientComponent />
      <Image
        resizeMode="contain"
        source={localAssets.appLogo}
        style={splashScreenStyle.imageStyle}
      />
    </View>
  );
};

export default SplashScreen;
