import React, { Component } from 'react';
import { Dimensions, Image, Modal, Pressable, Text, View } from 'react-native';
import { localAssets } from '../../../resources/assets/assets';
import { AlertDialogStyle } from '../AlertDialog.style';

interface AlertButton {
    title: string,
    onPress: () => void,
    color?: string
}

export interface AlertDialogProps {
    visible?: boolean,
    topimage?: any,
    title?: string,
    message?: string,
    positiveButton?: AlertButton,
    negativeButton?: AlertButton,
    neutralButton?: AlertButton,
    cancelable?: boolean,
    extraView?: any,
    onRef?: (ref: AlertDialog) => void,
    onDismiss?: () => void,
    onTouchOutside?: () => void,
    width?: any,
    height?: any,
    error?: any,
    customImage?: any
}

export default class AlertDialog extends Component<AlertDialogProps> {

    state = {};

    static dialogInstance: AlertDialog;
    static show(config: AlertDialogProps) {
        this.dialogInstance.showDialog(config);
    }

    static hide() {
        this.dialogInstance.hideDialog();
    }
    hideDialog = () => {
        this.setState({
            visible: false,
        });
    };
    componentDidMount() {
        if (this.props.onRef != null) {
            this.props.onRef(this);
        }
        Dimensions.addEventListener('change', ({ window: { width, height } }) => {
            this.setState({ width: width, height: height });
        }
        );
    }


    showDialog(config: AlertDialogProps) {

        this.setState({
            visible: true,
            title: config.title,
            topimage: config.topimage,
            message: config.message,
            positiveButton: config.positiveButton,
            negativeButton: config.negativeButton,
            neutralButton: config.neutralButton,
            cancelable: config.cancelable,
            children: config.extraView,
            error: config.error,
            customImage: config.customImage,
        });
    }


    render() {

        let { visible, customImage, topimage, title, message, positiveButton, negativeButton,
            error } = { ...this.props, ...this.state };
        return (
            <View style={AlertDialogStyle.dialog}>

                <Modal
                    visible={visible || false}
                    transparent
                    animationType="none"
                    hardwareAccelerated>

                    <View style={AlertDialogStyle.centered_view}>
                        <View style={[AlertDialogStyle.bell_modal]}>

                            {topimage != null && <Image source={topimage} style={AlertDialogStyle.top_img} />}

                            <View style={topimage != null ? [AlertDialogStyle.headerView, { alignSelf: 'center' }] : AlertDialogStyle.headerView}>
                                {topimage == null && <Image source={customImage ? customImage : localAssets.alertLogout} />}
                                <Text style={AlertDialogStyle.titleText}>{title}</Text>
                            </View>

                            <View style={AlertDialogStyle.btnView}>
                                <Text style={topimage != null ? [AlertDialogStyle.msgText, { alignSelf: 'center' }] : AlertDialogStyle.msgText}>{message}</Text>

                                {positiveButton && negativeButton ? <View style={AlertDialogStyle.twoButtonsView}>

                                    <Pressable style={AlertDialogStyle.negativeBtn}
                                        onPress={negativeButton?.onPress}
                                    >
                                        <Text style={AlertDialogStyle.negativeTextStyle}>{negativeButton.title}</Text>
                                    </Pressable>

                                    <Pressable style={AlertDialogStyle.positiveBtn}
                                        onPress={positiveButton?.onPress}
                                    >
                                        <Text style={AlertDialogStyle.positiveTextStyle}>{positiveButton.title}</Text>
                                    </Pressable>

                                </View>
                                    : positiveButton ?
                                        <View style={AlertDialogStyle.positive}>
                                            <Pressable style={[AlertDialogStyle.positiveBtn, {
                                                width: '100%',
                                            }]}
                                                onPress={positiveButton?.onPress}
                                            >
                                                <Text style={AlertDialogStyle.positiveTextStyle}>{positiveButton.title}</Text>
                                            </Pressable>

                                        </View> : null
                                }
                                {error && <Text style={AlertDialogStyle.errorText}>{error}</Text>}
                            </View>

                        </View>

                    </View>
                </Modal>
            </View>
        );
    }
}
