/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/react-in-jsx-scope */

import { FlatList, Image, KeyboardAvoidingView, Platform, Pressable, ScrollView, StatusBar, Text, View } from 'react-native';
import { RegistrationStyle } from './Registration.style';
import Header from '../common/ui/Header';
import { primaryTop } from '../../resources/values/color';
import { StatusbarStyle } from '../../utils/statusbarStyle';
import { localAssets } from '../../resources/assets/assets';
import { goBack } from '../../routes/Navigator';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import translations from '../../resources/locales/translations';
import RegularButton from '../common/ui/RegularButton';
import CustomBottomSheet from '../common/ui/CustomBottomSheet';
import InputRegularText from '../common/ui/InputRegularText';


const RegistrationScreen = () => {
    const [bottomSheetVisible, setBottomSheetVisible] = useState(false);
    const toggleBottomSheet = () => {
        setBottomSheetVisible(!bottomSheetVisible);
    };

    // Start manage localization
    const { appLanguage } = useSelector((state: any) => state.Appreducer);
    const [language, setLanguage] = useState<'en' | 'zh'>('en');
    useEffect(() => {
        setLanguage(appLanguage);
    }, []);
    const str = (key: keyof typeof translations.en): string => {
        return translations[language][key];
    };
    // End manage localization

    return (
        <View
            style={RegistrationStyle.mainView}
        >
            <StatusBar backgroundColor={primaryTop} barStyle={StatusbarStyle.LIGHT} hidden={false} />
            <View style={RegistrationStyle.mobileOuter}>
                <Header
                    styleMain={RegistrationStyle.mainToolBar}
                    sourceLeftImage={localAssets.backArrow}
                    onPressLeft={() => goBack()}
                    styleCenterText={RegistrationStyle.centerTextHeader}
                    centerText={str('registration')}
                />
            </View>
            <KeyboardAvoidingView
                behavior={Platform.OS === 'ios' ? 'padding' : Platform.OS === 'android' ? 'height' : 'padding'}
                style={RegistrationStyle.mainOuter}
            >
                <ScrollView
                    scrollEnabled={true}
                    showsVerticalScrollIndicator={false}
                    style={RegistrationStyle.mainScroll}
                    keyboardShouldPersistTaps="handled"
                >
                    <Image
                        source={localAssets.dummy1}
                        style={RegistrationStyle.courseImage}
                        resizeMode={'stretch'}
                    />
                    {/* Payment */}
                    <Text style={RegistrationStyle.paymentText}>{str('payment')}</Text>
                    <View style={RegistrationStyle.paymentType}>
                        <Text style={RegistrationStyle.hintText}>{str('paymentType')}</Text>
                        <Pressable onPress={() => { toggleBottomSheet(); }}>
                            <Image
                                style={RegistrationStyle.paymentImage}
                                source={localAssets.back}
                                resizeMode={'contain'}
                            />
                        </Pressable>
                    </View>
                    {/* Upload Slip */}
                    <Text style={RegistrationStyle.paymentText}>{str('uploadSlip')}</Text>
                    <Pressable onPress={() => { }}>
                        <View style={RegistrationStyle.uploadSlip}>
                            <Image
                                source={localAssets.upload}
                                style={RegistrationStyle.uploadSlipImage}
                                resizeMode={'contain'}
                            />
                            <Text style={RegistrationStyle.uploadSlipText}>{str('uploadSlip')}</Text>
                        </View>
                    </Pressable>
                    {/* Remark */}
                    <Text style={RegistrationStyle.remarkText}>{str('remark')}</Text>
                    <InputRegularText
                        returnKeyType={'next'}
                        placeholder={str('textHere')}
                        isPasswordShow={false}
                        border
                        onChangeText={() => {
                        }}
                        onSubmitEditing={() => {
                        }}
                    />
                    {/* Signature */}
                    <Text style={RegistrationStyle.signatureText}>{str('signature')}</Text>
                    <View style={RegistrationStyle.signature}>
                        <Image
                            style={RegistrationStyle.signatureImage}
                            source={localAssets.dummysign}
                            resizeMode={'contain'}
                        />
                        <Image
                            source={localAssets.refreshArrow}
                            style={RegistrationStyle.refreshImage}
                            resizeMode={'contain'}
                        />
                    </View>
                    <RegularButton
                        title={str('add')}
                        border={false}
                        onPress={() => { }}
                    />
                </ScrollView>
            </KeyboardAvoidingView>
            <CustomBottomSheet
                isVisible={bottomSheetVisible}
                onPressBtn={toggleBottomSheet}
                title={str('paymentType')}
                btnText={str('cancel')}
                children={<BottomSheetContent />}
            />
        </View>
    );
};

const BottomSheetContent = () => {
    const [activePayment, setActivePayment] = useState(String);

    // Start manage localization
    const { appLanguage } = useSelector((state: any) => state.Appreducer);
    const [language, setLanguage] = useState<'en' | 'zh'>('en');
    useEffect(() => {
        setLanguage(appLanguage);
    }, []);
    const str = (key: keyof typeof translations.en): string => {
        return translations[language][key];
    };
    // End manage localization

    const paymentTypes = [
        {
            id: '1',
            name: str('bankTransfer'),
        },
        {
            id: '2',
            name: str('creditCard'),
        },
        {
            id: '3',
            name: str('other'),
        },
    ];


    return (
        <View>
            <FlatList
                style={RegistrationStyle.flatList}
                data={paymentTypes}
                renderItem={({ item }) => {
                    return (
                        <Pressable onPress={() => { setActivePayment(item.name); }}
                            style={activePayment === item.name ? RegistrationStyle.paymentTypeContainerSelected : RegistrationStyle.paymentTypeContainer}>
                            <Text style={RegistrationStyle.paymentTypeTitle}>
                                {item.name}
                            </Text>
                        </Pressable>
                    );
                }}
            />
        </View>
    );
};

export default RegistrationScreen;
